import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';

// Import the AF2 Module
import { AngularFireModule } from 'angularfire2';
import { AngularFireDatabaseModule } from 'angularfire2/database';
import { AngularFireAuthModule } from 'angularfire2/auth';
// AF2 credentials
import { FIREBASE_CREDENTIALS } from './firebase.credentials';

// Ionic native
import { Camera } from '@ionic-native/camera';

import { MyApp } from './app.component';
import { AuthService } from '../providers/auth.service';
import { DataService } from '../providers/data.service';
import { StorageService } from '../providers/storage.service';

@NgModule({
  declarations: [
    MyApp
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/artist/artist.module.ngfactory#ArtistPageModuleNgFactory', name: 'ArtistPage', segment: 'artist', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/draw/draw.module.ngfactory#DrawPageModuleNgFactory', name: 'DrawPage', segment: 'draw', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/guess/guess.module.ngfactory#GuessPageModuleNgFactory', name: 'GuessPage', segment: 'guess', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/login/login.module.ngfactory#LoginPageModuleNgFactory', name: 'LoginPage', segment: 'login', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/profile/profile.module.ngfactory#ProfilePageModuleNgFactory', name: 'ProfilePage', segment: 'profile', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/tabs/tabs.module.ngfactory#TabsPageModuleNgFactory', name: 'TabsPage', segment: 'tabs', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/list/list.module.ngfactory#ListPageModuleNgFactory', name: 'ListPage', segment: 'list', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/results/results.module.ngfactory#ResultsPageModuleNgFactory', name: 'ResultsPage', segment: 'results', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/register/register.module.ngfactory#RegisterPageModuleNgFactory', name: 'RegisterPage', segment: 'register', priority: 'low', defaultHistory: [] }
  ]
}),
    AngularFireModule.initializeApp(FIREBASE_CREDENTIALS),
    AngularFireDatabaseModule,
    AngularFireAuthModule
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp
  ],
  providers: [
    StatusBar,
    SplashScreen,
    Camera,
    { provide: ErrorHandler, useClass: IonicErrorHandler },
    AuthService,
    DataService,
    StorageService
  ]
})
export class AppModule { }



